import React, { useEffect, useState } from 'react';

const sliderImages = [
  process.env.PUBLIC_URL + '/images/1000278926.jpg',
  process.env.PUBLIC_URL + '/images/1000278927.jpg',
  process.env.PUBLIC_URL + '/images/1000278928.jpg',
  process.env.PUBLIC_URL + '/images/1000278929.jpg',
];

function isMobile() {
  return window.innerWidth <= 600;
}

const ProductList = ({ onAddToCart }) => {
  const [current, setCurrent] = useState(0);
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [slideDirection, setSlideDirection] = useState('up');

  useEffect(() => {
    fetch('products.json')
      .then(res => res.json())
      .then(data => {
        setProducts(data);
        setLoading(false);
      });
  }, []);

  // Only auto-slide on desktop
  useEffect(() => {
    if (!isMobile()) {
      const interval = setInterval(() => {
        setSlideDirection('up');
        setCurrent(prev => (prev + 1) % sliderImages.length);
      }, 2500);
      return () => clearInterval(interval);
    }
  }, []);

  if (loading) return <p>Loading products...</p>;

  const product = products[current] || {};

  // Manual slide for mobile
  const handleManualSlide = () => {
    setSlideDirection('up');
    setCurrent(prev => (prev + 1) % sliderImages.length);
  };

  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', margin: '2rem 0' }}>
      <div
        className={`slider-image-container ${slideDirection}`}
        style={{
          position: 'relative',
          width: '100vw',
          maxWidth: 420,
          height: '60vh',
          maxHeight: 600,
          overflow: 'hidden',
          borderRadius: 18,
          boxShadow: '0 4px 24px rgba(0,0,0,0.08)',
          marginBottom: 24,
          touchAction: 'manipulation',
        }}
        onClick={isMobile() ? handleManualSlide : undefined}
      >
        <img
          src={sliderImages[current]}
          alt={product.title || 'Product'}
          className="slider-image"
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'cover',
            borderRadius: 18,
            transition: 'transform 0.7s cubic-bezier(0.4,0,0.2,1)'
          }}
        />
        <div style={{
          position: 'absolute',
          bottom: 0,
          left: 0,
          width: '100%',
          background: 'rgba(0,0,0,0.5)',
          color: '#fff',
          padding: '12px 0',
          textAlign: 'center',
          fontWeight: 700,
          fontSize: 20,
          borderBottomLeftRadius: 18,
          borderBottomRightRadius: 18
        }}>
          {product.title}
        </div>
        {isMobile() && (
          <button
            onClick={handleManualSlide}
            style={{
              position: 'absolute',
              bottom: 70,
              left: '50%',
              transform: 'translateX(-50%)',
              background: 'rgba(0,0,0,0.5)',
              color: '#fff',
              border: 'none',
              borderRadius: 24,
              padding: '10px 28px',
              fontSize: 16,
              fontWeight: 700,
              cursor: 'pointer',
              zIndex: 2
            }}
          >
            Slide Up
          </button>
        )}
      </div>
      <button onClick={() => onAddToCart(product)} style={{
        background: 'linear-gradient(90deg, #007bff 60%, #00c6ff 100%)',
        color: '#fff',
        border: 'none',
        borderRadius: 24,
        padding: '12px 36px',
        fontSize: 18,
        fontWeight: 700,
        boxShadow: '0 2px 8px rgba(0,123,255,0.08)',
        cursor: 'pointer'
      }}>
        Add to Cart
      </button>
      <style>{`
        @media (max-width: 600px) {
          .slider-image-container {
            width: 100vw !important;
            height: 100vh !important;
            max-width: 100vw !important;
            max-height: 100vh !important;
            border-radius: 0 !important;
            margin-bottom: 0 !important;
          }
          .slider-image {
            border-radius: 0 !important;
          }
        }
        .slider-image-container.up .slider-image {
          animation: slideUp 0.7s cubic-bezier(0.4,0,0.2,1);
        }
        @keyframes slideUp {
          0% { transform: translateY(100%); }
          100% { transform: translateY(0); }
        }
      `}</style>
    </div>
  );
};

export default ProductList;